$ratio: 750/640;
$browser-default-font-size: 14;
$browser-default-font-size-px: 14px !default;
$lte7:                  true !default; //是否兼容ie6-7

@function r($px, $isFixed:true) {
  @if $px != 0 {
    @return if($isFixed, $px / $ratio + 0.01, $px / $ratio) / $browser-default-font-size * 1rem;
  } 
  @else
  {
    @return 0;
  }
}
$designWidth : 750; //如设计图是750
$deviceWidth : 320;//视图的宽度
@function pxToRem( $px ){
    @return $px * $deviceWidth / $designWidth / $browser-default-font-size * 1rem;; 
}


.comment-inner {
  .comment-score {
    display: flex;
    padding: r(15) r(25);
    border-top: r(10) solid #f6f6f6;
    border-bottom: 1px solid #d5d5d5;
    .taste-star-wrap,.package-star-wrap {
      margin: 0 r(15);
      color: #ffb000;
    }
    
    .mail-score-content {
      display: flex;
      flex-direction: column;
    }

    .other-score-content {
      display: flex;
      flex-direction: column;
      flex: 1;
      justify-content: space-around;
    }
    .package-score, .taste-score {
      display: flex;
      justify-content: center;

    }
    .mail-score {
      font-size: r(32);
      color: #ffb000;
      margin-bottom: r(5);
    }
    .mail-text {
      font-size: r(12);
      color: #656565;
    }
    .taste-text, .package-text {
      font-size: r(12);
      color: #554343;
    }
    .taste-score-text, .package-score-text {
      font-size: r(15);
      color: #ffb000;
    }
    .send-score-content {
      display: flex;
      flex-direction: column;
      margin-top: r(3);
    }
    .send-score {
      font-size: r(32);
      color: #656565;
      margin-bottom: r(5);
    }
    .send-text {
      font-size: r(12);
      color: #999;
    }
  }

}